.basic-1 {
  padding-top: 8rem;
  padding-bottom: 3rem;
}

.basic-1 .text-container {
  margin-bottom: 3rem;
}

.basic-1 .section-title {
  margin-bottom: 0.5rem;
}

.basic-1 h2 {
  margin-bottom: 1.375rem;
}

.basic-1 .testimonial-author {
  color: #484a46;
}
.basic-1 .image-container {
  overflow: hidden;
  border-radius: 0.25rem;
}

.basic-1 .image-container img {
  margin: 0;
  border-radius: 0.25rem;
  transition: all 0.3s;
}

.basic-1 .image-container:hover img {
  -moz-transform: scale(1.08);
  -webkit-transform: scale(1.08);
  transform: scale(1.08);
}

.cards-1 {
  padding-top: 3rem;
  padding-bottom: 2.875rem;
}

.cards-1 .card {
  margin-bottom: 2.5rem;
  border: none;
  text-align: center;
}

.cards-1 .fa-stack {
  width: 6rem;
  height: 6rem;
  margin-right: auto;
  margin-bottom: 1rem;
  margin-left: auto;
  line-height: 6rem;
  text-align: left;
}

.cards-1 .fa-stack .hexagon {
  position: absolute;
  width: 6rem;
  height: 6rem;

  background-size: 6rem 6rem;
}

.cards-1 .fa-stack-1x {
  color: #fff;
  font-size: 2rem;
}

.cards-1 .card-body {
  padding: 0;
}

.cards-1 .card-title {
  margin-bottom: 0.625rem;
}

